/* NAVIGATION */
.Navigation {
  align-items: center;
  background: #fff;
  display: flex;
  position: relative;
  margin: 0 auto;
  justify-content: center;
  text-align: center;
}

.Nav-list {
  margin: 0;
  padding: 0;
}
.Nav-list__item {
  display: inline-block;
  padding: 5px 8px;
  margin: 0 5px;
}
.Nav-list__link {
  color: #2d9ec5;
  display: block;
  font-size: 18px;
  text-decoration: none;
}


@media screen and (min-width: 720px) {
  .Navigation {
    padding: 5px 15%;
  }
}
